@media screen and (max-width: 1400px) {
  #profile {
    height: 90vh;
    margin-bottom: 6rem;
  }
  .about-containers {
    flex-wrap: wrap;
  }
  #contact,
  #projects {
    height: -moz-fit-content;
    height: fit-content;
  }
}
@media screen and (max-width: 1200px) {
  #desktop-nav {
    display: none;
  }
  #hamburger-nav {
    display: flex;
  }
  #experience,
  .experience-details-container {
    margin-top: 2rem;
  }
  #profile,
  .section-container {
    display: block;
  }
  .arrow {
    display: none;
  }
  section,
  .section-container {
    height: -moz-fit-content;
    height: fit-content;
  }
  section {
    margin: 0 5%;
  }
  .section__pic-container {
    width: 275px;
    height: 375px;
    margin: 2rem auto;
    justify-content: center;
  }
  .about-containers {
    margin-top: 10%;
  }
}
@media screen and (max-width: 800px) {
  /* #about {
      margin-top: 10%;
    } */
  .about-containers {
    margin-bottom: 4rem;
  }
  #technology-container img,
  #technology-container svg {
    height: 3rem;
    margin: 0 0.5rem;
  }
}
@media screen and (max-width: 555px) {
  #contact,
  footer {
    height: 40vh;
  }
  #profile {
    height: 90vh;
    margin-bottom: 0;
  }
  #technology-container img,
  #technology-container svg {
    height: 2rem;
    margin: 0 0.25rem;
  }
  article {
    font-size: 1rem;
  }
  footer nav {
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 2rem;
  }
  .about-containers,
  .contact-info-upper-container,
  .btn-container {
    flex-wrap: wrap;
  }
  .contact-info-container {
    margin: 0;
  }
  .contact-info-container p,
  .nav-links li a {
    font-size: 1rem;
  }
  .experience-sub-title {
    font-size: 1.25rem;
  }
  .logo {
    font-size: 1.5rem;
  }
  .nav-links {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
  .section__pic-container {
    width: auto;
    justify-content: center;
  }
  .section__text__p2 {
    font-size: 1.25rem;
  }
  .title {
    font-size: 2rem;
  }
  .text-container {
    text-align: justify;
  }
}
@media screen and (max-width: 330px) {
  .section__pic-container img {
    height: 23.5vh;
  }
}
